  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
#div-furret{
    margin-top:18%;
}
#div-function{
  position: fixed;
  left: 49%;
  bottom: 0px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
